/* Dashboard */

.summary > li {
  border: 0.1rem #c0c0c0 solid;
  margin: 2rem;
  border-radius: 0.5rem;
  -webkit-flex: 1 1 20rem;
          flex: 1 1 20rem;
}
.summary-title {
  font-size: 2rem;
  padding: 1rem;
}
.summary-body {
  font-size: 4rem;
  padding: 1rem;
  text-align: center;
}

.summary-title.color1 {
  background-color: #f0e0e0;
}
.summary-title.color2 {
  background-color: #e0f0e0;
}
.summary-title.color3 {
  background-color: #e0e0f0;
}

/* Chatbox */

.support-users {
  background: #f0f0f0;
  height: 90%;
}
.support-users li {
  background-color: #f8f8f8;
}
.support-users button {
  background-color: transparent;
  border: none;
  text-align: left;
}
.support-users li {
  margin: 0;
  background-color: #f0f0f0;
  border-bottom: 0.1rem #c0c0c0 solid;
}

.support-users li:hover {
  background-color: #f0f0f0;
}
.support-users li.selected {
  background-color: #c0c0c0;
}
.support-messages {
  padding: 1rem;
}
.support-messages input {
  /* width: calc(100% - 10%); */
}
.support-messages ul {
  height: calc(90vh - 18rem);
  max-height: calc(90vh - 18rem);
  overflow: scroll;
}
.support-messages li {
  margin-bottom: 1rem;
}

.support-users span {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  position: absolute;
  margin-left: -25px;
  margin-top: 10px;
}
.support-users .offline {
  background-color: #808080;
}
.support-users .online {
  background-color: #20a020;
}
.support-users .unread {
  background-color: #f02020;
}

html {
  font-size: 62.5%; /* 16px x 62.5 = 10px = 1rem */
  box-sizing: border-box;
}
body {
  margin: 0;
  height: 100vh;
  /* font-size: 1.8rem; */
  font-size: 18px !important;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
#root {
  height: 100%;
}

/* Layout */
.grid-container {
  display: grid;
  grid-template-areas:
    "header"
    "main"
    "footer";
  grid-template-columns: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}

.btn-header {
  width: 30%;
  float: left;
  /* margin-top: -2rem; */
  height: 50%;
}
.name-categories {
  color: #e0e0ff;
  font-size: 30px;
  margin-top: 35px;
  text-align: center;
  margin-left: 1rem;
}

header {
  grid-area: header;
  background-color: #203040;
}
main {
  padding-top: 24px;
  grid-area: main;
  background: linear-gradient(70deg, #eeffe0, rgb(199, 245, 157));
  /* padding: 1rem; */
}
footer {
  grid-area: footer;
  background-image: url("https://t4.ftcdn.net/jpg/02/44/71/07/360_F_244710705_kJbRGNfdwXq4zfikaaXE77iQCAHsQjt0.jpg");
  color: #ffffff;
  /* opacity: 0.5;ss */
  background-repeat: no-repeat;
  background-size: cover;
  height: 370px;
}
.img-footer {
  width: 200px;
  height: 100px;
  text-align: center;
  margin-top: "-5rem";
  border-radius: 20%;
}
.content-footer {
  text-align: center;
}
.icon-footer {
  padding: 2rem;
  border-radius: 50%;
  width: 80px;
  font-size: 21px;
  margin: 1rem;
  border: 3px solid #ffffff;
}
.fa-facebook:hover {
  background-color: cornflowerblue;
}

.fa-twitter:hover {
  background-color: rgb(13, 94, 245);
}

.fa-google:hover {
  background-color: rgb(245, 164, 13);
}
/* Common */
h1 {
  font-size: 1.8rem;
  padding: 1rem 0;
}
h2 {
  font-size: 1.6rem;
  padding: 1rem 0;
}

a {
  text-decoration: none;
  /* transition: background-color 0.5s; */
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  font-size: 24px;
  font-family: "Courier New", Courier, monospace;
  font-style: italic;
}
a:hover {
  color: #00eeff;
  -webkit-transform: translateY(-10px);
          transform: translateY(-10px);
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
li {
  margin-top: 1rem;
}
input,
select,
textarea,
button {
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #a4a4a4 solid;
  font-size: 1.6rem;
  font-family: Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #0c0c0c;
}
button {
  background-color: #f8f8f8;
  cursor: pointer;
}
input:hover,
select:hover,
textarea:hover,
button:hover {
  border: 0.1rem #4bff04 solid;
}

button.primary {
  background-color: #f0c040;
}
button.block {
  width: 100%;
}
button.small {
  font-size: 1.2rem;
}

/* Header */
header a {
  color: #ffffff;
  padding: 1rem;
}
a.brand {
  margin-left: 6rem;
  /* color: #ffffff;
  font-size: 3rem;
  font-weight: bold; */
  position: relative;
  display: inline-block;
  font-size: 2rem;
  /* font-weight: 800; */
  color: rgb(243, 243, 243);
  overflow: hidden;
  background: linear-gradient(
    to right,
    rgb(252, 252, 244),
    rgb(150, 149, 149) 50%,
    rgb(231, 231, 224) 50%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-size: 200% 100%;
  background-position: 100%;
  transition: background-position 275ms ease;
  text-decoration: none;
}
a.brand:hover {
  background-position: 0 100%;
}
.badge {
  background-color: #f02020;
  color: #ffffff;
  border-radius: 50%;
