/* Aside */
aside {
  position: fixed;
  width: 30rem;
  height: 100%;
  background-color: #efefef;
  z-index: 1000;
  -webkit-transform: translateX(-30rem);
          transform: translateX(-30rem);
  transition: all 0.5s;
}
aside.open {
  -webkit-transform: translateX(0);
          transform: translateX(0);
  /* background-image: url("https://crossware.co.nz/wp-content/uploads/2020/09/cute-food-dancing-gif.gif"); */
  background: linear-gradient(40deg, rgb(106, 248, 50), rgb(36, 230, 255));
  background-size: cover;
  width: 380px;
  text-align: justify;
}
button.open-sidebar {
  font-size: 2rem;
  padding: 0.2rem 0.5rem;
  margin: 2.5rem;
  background: none;
  color: #ffffff;
  cursor: pointer;
  margin-top: -2rem;
  position: absolute;
  bottom: 2px;
  left: 0;
  margin-right: 2rem;
}
button.open-sidebar:hover {
  border-color: #ffffff;
}
aside ul {
  padding: 0;
  list-style: none;
}
aside li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem;
}
button.close-sidebar {
  padding: 0.3rem 0.8rem;
  margin-top: 1rem;
}

/* Image */
img {
  border-radius: 0.5rem;

}
img.small {
  max-width: 300px;
  width: 100%;
}
img.medium {
  box-shadow: 1px 13px 22px #333;
  /* max-width: 29rem; */
  margin-top: 10px;
  width: 100%;
  /* padding-top: 40px; */
  height: 300px;
}
img.medium:hover {
  -webkit-transition: transform 0.5s;
  -moz-transition: transform 0.5s;
  -o-transition: transform 0.5s;
}
img.large {
  width: 100%;
}

/* Card */
.card {
  width: 18%;
  height: 450px;
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}
.card-cover {
  margin-top: 30px;
}

.img-card {
  width: 300px;
  margin: 2rem;
  float: left;
  border-radius: 70%;
}
.box {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  -moz-osx-font-smoothing: grayscale;
  transition-duration: 0.3s;
  transition-property: box-shadow, -webkit-transform;
  transition-property: box-shadow, transform;
  transition-property: box-shadow, transform, -webkit-transform;
}

.box:hover,
.box:focus,
.box:active {
  box-shadow: 10px 10px 5px rgba(0, 0, 0, 0.2);
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
.img-card:hover {
}

