//mixins
@mixin align {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateY(-50%) translateX(-50%);
}

//presentation
.container {
  position: relative;
  .page-title,
  .page-sub-title {
    color: white;
    text-align: center;
  }
  .page-title {
    margin: 30px 0 5px;
    font-size: 30px;
  }
  .page-sub-title {
    font-weight: 100;
    margin: 0 0 30px 0;
    font-size: 23px;
  }
}

//variables
$pink: #de6262;
$dark: #333741;
$peach: #ffb88c;
$green: #52c234;

// html {
//   background: linear-gradient(45deg, $pink 10%, $peach 90%);
//   font-family: "HelveticaNeue-Light", "Helvetica Neue Light", "Helvetica Neue",
//     Helvetica, Arial, "Lucida Grande", sans-serif;
//   height: 100vh;
